  text-align: center;
}
.message {
  color: rgb(71, 71, 87);
}

.footer {
  height: 50px;
  color: beige;
  background-color: #2a243a;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding-top: 10px; 
}

